
document.addEventListener("DOMContentLoaded",function(){
    iniciarApp();
});

function iniciarApp(){
    navegacionFija();
    crearGaleria();
    scrollnav();
}

function navegacionFija(){
    const barra = document.querySelector(".header");
    const festival = document.querySelector(".sobre-festival");
    const scroll_body = document.querySelector("body");

    window.addEventListener("scroll",function(){

        if(festival.getBoundingClientRect().top <0){
            barra.classList.add("fijo");
            scroll_body.classList.add("body-scroll");
        }else{
            barra.classList.remove("fijo");
            scroll_body.classList.remove("body-scroll");
        }
    });


}

//CONFIGURANDO API  DE SCROLL
function scrollnav (){
    const enlaces = document.querySelectorAll(".navegacion-princial a");

    enlaces.forEach(enlace => {
        
        enlace.addEventListener("click",function(e){
            e.preventDefault();

            const scrollseccion = e.target.attributes.href.value;
            const seccion = document.querySelector(scrollseccion);

            seccion.scrollIntoView({behavior:"smooth"});

        });

    });

};

function crearGaleria(){
    const galeria = document.querySelector(".galeria-imagenes");
    

    for (let i = 1; i<=12; i++) {
        const imagen = document.createElement('picture');
        imagen.innerHTML=`
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen galeria">
    
        `;

        imagen.onclick = function(){
            mostrarimagen(i);
        }
        galeria.appendChild(imagen);   
    }
}

function mostrarimagen(idimagen){
    const imagen = document.createElement("picture");
    imagen.innerHTML = `
        <source srcset="build/img/grande/${idimagen}.avif" type="image/avif">
        <source srcset="build/grande/${idimagen}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/grande/${idimagen}.jpg" alt="imagen galeria">
    `;

    // CREANDO OVERLAY Y AGREGANDO IMAGEN
    const overlay = document.createElement("DIV"); //creamos in div para el fondo
    overlay.appendChild(imagen); //Le agregamos las imagenes al div
    overlay.classList.add("overlay-imagen");// Le daos una clase al div


    overlay.onclick = function(){
        overlay.remove(); //cerrar ventana modal

        // quitar clase: detener-scroll
        const body = document.querySelector("body");
        body.classList.remove("detener-scroll");
    }

    // CREANDO BOTON CERRAR
    const btn_cerrar = document.createElement("P");
    btn_cerrar.classList.add("btn_cerrar")
    btn_cerrar.textContent ="X";
    overlay.appendChild(btn_cerrar);

    // FUNCION PARA CERRAR VENTANA MODAL
    btn_cerrar.onclick  = function(){
        overlay.remove(); //cerrar ventana modal

        // quitar clase: detener-scroll
        const body = document.querySelector("body");
        body.classList.remove("detener-scroll")
    }


    // AGREGANDO OVERLY AL BODY
    const body = document.querySelector("body"); // seleccionamos el body y guardamos 
    body.classList.add("detener-scroll")
    body.appendChild(overlay); // Se agrega el div al body
    

}